import { Injectable } from '@angular/core';
import { APIName } from 'shared/API/APIName';
import { DataService } from '../DataService/data.service';
import { Observable } from 'rxjs';
import { IMainResponse } from '../../Models/Interfaces/Main/MainResponse';
import { ERequestTypes } from '../../Models/Enums/RequestTypes/RequestTypes';
import { IApiData } from '../../Models/Interfaces/APIs/apiData.model';

@Injectable({
	providedIn: 'root'
})
export class DoctorsService {
	Req = { requestType: ERequestTypes.VIEW, services: [] } as IApiData<any>;
  /**
   * The Requirements For The Construction Of The service
   * @param dataService for managing Http methods
   */
	constructor(private dataService: DataService) { }
  /**
   * get doctor list
   * @return Observable<IMainResponse>
   */
	getDoctorList(): Observable<IMainResponse> {
		this.Req.services[0] = {
					ConditionsAnded: false,
					columns: [],
					serviceName: "VIEW_SCAN_CENTER_DOCTORS",
		};
		return this.dataService.post(APIName.doctors, this.Req);
	}
}
