import { Injectable } from '@angular/core';
import {
	Resolve,
	RouterStateSnapshot,
	ActivatedRouteSnapshot
} from '@angular/router';
import { Observable } from 'rxjs';
import { IMainResponse } from '../../Models/Interfaces/Main/MainResponse';
import { DoctorsService } from './../../Services/OrderServices/doctors.service';

@Injectable({
	providedIn: 'root'
})
export class DoctorListResolver implements Resolve<boolean> {
  /**
   * The Requirements For The Construction Of The resolver
   * @param service for DoctorsService
   */
	constructor(private service: DoctorsService) {
	}
  /**
   * get Doctors list
   * @param route for ActivatedRouteSnapshot
   * @param state for RouterStateSnapshot
   * @return Observable<any>
   */
	resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> {
		return this.service.getDoctorList();
	}
}
