import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import { DoctorListResolver } from 'projects/connect/src/app/Resolvers/Doctor-List/doctor-list.resolver';
import { OrderDetailsResolver } from 'projects/connect/src/app/Resolvers/Order-Details/order-details.resolver';
import {AddOrderComponent} from './add-order/add-order.component';


const routes: Routes = [
  {
    path: '',
    component: AddOrderComponent,
    data: {breadcrumbs: ['Add New Order']},
    resolve: {
      DoctorList: DoctorListResolver
    }
  },
  {
    path: ':orderId',
    component: AddOrderComponent,
    data: {breadcrumbs: ['Add To Order']},
    resolve: {
      orderDetails: OrderDetailsResolver
    }
  }
];


@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ],
  providers: [
    OrderDetailsResolver
  ]
})
export class AddOrderPagesRoutingModule {
}
